<template>
  <!-- <div class="nav-bar" :class="chooesType==='2'?'navbars-bnt':''"> -->
    <mt-navbar v-model="selected">
      <mt-tab-item v-for="(item,index) in navbars" :key="index" :id="item.index">
        <img v-if="chooesType==='1'" slot="icon" :src="item.navbarImg" alt="navbarImg" class="navbar-img">
        <span class="navbar-title" :class="chooesType==='2'?'navbars-bnt':''">{{item.navbarTitle}}</span>
      </mt-tab-item>
    </mt-navbar>
  <!-- </div> -->
</template>
<script>
export default {
  data() {
    return {
      selected: ""
    };
  },
  props: {
    navbars: {
      type: Array,
      default: [],
      requried: true
    },
    chooesType: {
      type: String,
      default: '1',
      requried: true
    }
  }
};
</script>
<style scoped>
.navbars-bnt {
  width: 4.031rem;
  height: 1.781rem;
  background-color: #ffffff;
  border-radius: 0.188rem;
  border: solid 0.05rem #e5e3e3;
}
.nav-bar {
  width: 20.969rem;
  height: 4rem;
  margin: 0 auto;
}
.navbar-img {
  box-shadow: rgba(131, 87, 218, 0.47);
}
.navbar-title {
  height: 0.781rem;
  font-size: 0.79rem;
  margin: 0 auto;
}
.mint-navbar .mint-tab-item.is-selected {
  color: black;
  border-bottom: 0 solid #fff;
}
.mint-navbar .mint-tab-item {
  padding: 0;
}
</style>



