  <template>
  <div class="contain">
    <div class="special">
      <div class="contain-left" :class="containerType==='1'?'contain-left1':'contain-left2'">
        <p>{{containerhots.product}}</p>
        <p>{{containerhots.hot}}</p>
      </div>
      <div class="contain-right" @click="toP(containerhots)">
          <img class="icon-jiantou" src="static/images/p_handpick/more.png" alt="">
      </div>
    </div>
    <div class="img-dd" v-for="(item,index) in containImgs" :key="index">
      <div class="img-d1">
        <img class="img-url1" :src="item.imgUrl1" alt>
        <div class="musicgroup">
          <img class="musicsymbol" :src="item.musicsymbol" alt>
          <span class="musicmount">{{item.musicmount}}</span>
        </div>
        <span class="img-title1">{{$t('favorite.imgtitle')}}</span>
        <span class="img-desc1">{{$t('favorite.imgdesc')}}</span>
      </div>
      <div class="img-d2">
        <img class="img-url2" :src="item.imgUrl2" alt>
        <div class="music-group">
          <img class="music-symbol" :src="item.musicsymbol" alt>
          <span class="music-mount">{{item.musicmount}}</span>
        </div>
        <span class="img-title2">{{$t('favorite.imgtitle')}}</span>
        <span class="img-desc2">{{$t('favorite.imgdesc')}}</span>
      </div>
    </div>
  </div>
</template>
  <script>
export default {
  methods: {
    toP(containerhots) {
      this.$emit("callbackHeadline", containerhots); //路由子组件传到父组件(this.$emit)
    }
  },
  data() {
    return {};
  },
  props: {
    containImgs: {
      type: Array,
      // default:[],
      requried: true
    },
    containerhots: {
      type: Object,
      default: "",
      requried: true
    },
    containerType: {
      type: String,
      default: "1",
      requried: true
    }
  }
};
</script>
  <style lang="scss" scoped>
.special {
  display: flex;
  justify-content: space-between;
  margin: 1.125rem 0.75rem 0 0.844rem;
}
.special .contain-left2 {
  margin-top: 0;
}
.contain-left p:nth-child(1) {
  height: 1.063rem;
  font-size: 1.138rem;
  letter-spacing: 0.028rem;
  color: #030303;
}
.contain-left p:nth-child(2) {
  height: 0.781rem;
  font-size: 0.513rem;
  letter-spacing: -0.028rem;
  color: #555555;
  margin: 0.563rem 0 0.156rem 0;
}
.icon-jiantou {
  width: 1.25rem;
  height: 1.25rem;
  margin: 0.469rem 0 0 0;
}
.img-dd {
  display: flex;
  flex-direction: row;
  margin: 1.156rem 1.125rem;
}
.img-dd img {
  width: 10rem;
  height: 10rem;
}
.img-url1 {
  margin: 0 0.6565rem 0 0;
}
.img-url2,
.img-title2,
.img-desc2 {
  margin: 0 0 0 0.565rem;
}
.img-title1,
.img-title2 {
  display: block;
  height: 0.875rem;
  font-size: 0.9rem;
  letter-spacing: 0.009rem;
}
.img-desc1,
.img-desc2 {
  display: block;
  height: 0.719rem;
  font-size: 0.75rem;
  letter-spacing: -0.025rem;
  color: #aeaeae;
  margin-top: 0.406rem;
}
.musicgroup,
.music-group {
  position: relative;
  bottom: 1.688rem;
  left: 0.844rem;
  display: flex;
}
.musicgroup {
  margin-left: 0.3rem;
}
.music-group {
  margin-left: 0.844rem;
}
.img-dd .musicgroup img,
.img-dd .music-group img {
  width: 0.906rem;
  height: 0.906rem;
}
.img-dd .musicgroup span,
.img-dd .music-group span {
  line-height: 0.87rem;
  font-size: 0.87rem;
  color: #ffffff;
  margin: 0 0 0 0.594rem;
}
</style>



