<template>
  <div class="list">
    <ul>
      <li v-for="(item,index) in ranklists" :key="index">
        <span class="rank-list" :class="item.id==='1'?'rank-list1':item.id==='2'?'rank-list2':item.id==='3'?'rank-list3':item.id==='4'?'rank-list4':item.id==='5'?'rank-list5':''">
          {{item.id}}
        </span>
        <!-- 左 -->
        <p class="rank-left">
          <img :src="item.imgUrl" alt class="rank-img">
        </p>
        <!-- 中间 -->
        <p class="rank-center">
          <span class="title">{{item.title}}</span>
          <span class="desc">{{item.desc}}</span>
        </p>
        <!-- 右 -->
        <p class="rank-right">
          <img class="rank-symbol" :src="item.imgsymbol" alt @click="showPop()">
        </p>
      </li>
    </ul>
    <mt-popup v-model="popupVisible" position="bottom">
      <div class="tanchuang">
        <div class="x-edit">
          <span class="res-name">重命名</span>
          <span class="del">删除</span>
        </div>
        <span class="x-cancel" @click="cancel()">取消</span>
      </div>
    </mt-popup>
  </div>
</template>
<script>
export default {
  data() {
    return {
      popupVisible: false,
      ranklists: [
        {
          id: "1",
          title: this.$t('ranklist.ranklisttitle'),
          desc: this.$t('ranklist.ranklistdesc'),
          imgUrl: "static/images/play_list/曲目图-小.png",
          imgsymbol: "static/images/play_list/add.png"
        },
        {
          id: "2",
          title: this.$t('ranklist.ranklisttitle'),
          desc: this.$t('ranklist.ranklistdesc'),
          imgUrl: "static/images/play_list/曲目图-小-1.png",
          imgsymbol: "static/images/play_list/add.png"
        },
        {
          id: "3",
          title: this.$t('ranklist.ranklisttitle'),
          desc: this.$t('ranklist.ranklistdesc'),
          imgUrl: "static/images/play_list/曲目图-小2.png",
          imgsymbol: "static/images/play_list/added.png"
        },
        {
          id: "4",
          title: this.$t('ranklist.ranklisttitle'),
          desc: this.$t('ranklist.ranklistdesc'),
          imgUrl: "static/images/play_list/曲目图-小5.png",
          imgsymbol: "static/images/play_list/add.png"
        },
        {
          id: "5",
          title: this.$t('ranklist.ranklisttitle'),
          desc: this.$t('ranklist.ranklistdesc'),
          imgUrl: "static/images/play_list/曲目图-小-55.png",
          imgsymbol: "static/images/play_list/add.png"
        }
      ]
    };
  },
  methods: {
    showPop() {
      this.popupVisible = true;
    },
    cancel() {
      this.popupVisible = false;
    }
  }
};
</script>
<style lang="scss" scoped>
.rank-list {
  font-size: 1.238rem;
  margin: 1.6rem auto 0 2.225rem;
}
.rank-list1 {
  color: #b897a2;
}
.rank-list2 {
  color: #cbb7a9;
}
.rank-list3 {
  color: #7eacaa;
}
.rank-list4 {
  color: #888d84;
}
.rank-list5 {
  color: #8d8d8d;
}
ul li {
  display: flex;
  border-top: solid 0.05rem #f7f7f7;
  margin-top: 1.156rem;
}
.rank-left {
  margin: 0.813rem 0 0 1.5rem;
}
.rank-img {
  width: 3.125rem;
}
.rank-center {
  width: 100%;
}
.title {
  display: block;
  font-size: 0.938rem;
  letter-spacing: 0.072rem;
  color: #1c1b1b;
  margin: 0.906rem 0 0 0.875rem;
}
.desc {
  font-size: 0.688rem;
  letter-spacing: 0.053rem;
  color: #898989;
  margin: 0.25rem 0 0 0.75rem;
}
.rank-symbol {
  width: 1.25rem;
  margin: 1.625rem 1.563rem 0 0;
}

//弹窗
.tanchuang {
  width: 20.438rem;
  text-align: center;
}
.x-edit {
  height: 5rem;
  border: 0px solid #fff;
  border-radius: 8px;
  margin-bottom: 0.8rem;
  background: rgba(229, 229, 229, 0.8);
}
span {
  display: block;
}
.res-name {
  font-stretch: normal;
  color: #030303;
  height: 2.5rem;
  line-height: 2.5rem;
  border-bottom: 1px solid #d5d5d5;
}
.del {
  color: #030303;
  height: 2.5rem;
  line-height: 2.5rem;
}
.x-cancel {
  color: #030303;
  height: 2.5rem;
  line-height: 2.5rem;
  border: 0px solid #fff;
  border-radius: 8px;
  background: rgba(229, 229, 229, 0.8);
}
</style>



