<template>
  <div class="banner" :class="bannersType==='1'?'banner1':'banner2'">
    <!-- 我是轮播图 -->
    <!-- type:default 默认按钮的背景颜色/primary 天蓝色/danger 红色-->
    <!-- size:normal 默认大小/small 小尺寸/large 大尺寸 -->
    <!-- plain：鬼按钮，作用是带边框颜色和字体颜色，不带背景颜色,plain不给定逻辑值时不需要绑定 -->
    <!-- disabled：作用是禁用 -->
    <!-- icon:more是在文本前添加三个省略号，back是在文本前添加'<'符号 -->
    <mt-swipe :auto=time :speed="10" :defaultIndex="0" >
      <mt-swipe-item class="banner-url" v-for="(item,index) in banners" :key="index">
        <img :src="item.imgUrl" alt="banner图" class="banner-img">
      </mt-swipe-item>
    </mt-swipe>
    <!-- <mt-button class="toast" type="primary" size="large" icon="back" @click="clicks" :plain="true">按钮</mt-button> -->
  </div>
</template>

<script>
export default {
  data() {
    return {
    };
  },
  props:["banners","time","bannersType"]
  
};
</script>

<style scoped>
.banner {
  height: 11.969rem;
  margin: 1.56rem 0.75rem 0 0.75rem;
}
.banner-img {
  width: 100%;
  height: 10.5rem;
  border: 1px solid #fff;
  border-radius: 10px;
}
</style>



