<template>
  <div :class="searchType==='2'?'search-wp':''">
    <div class="search" @click="toSearch()" :class="searchType==='2'?'search-w':''" :af="false">
      <!-- <mt-search v-model="searchValue" cancel-text="取消" placeholder="搜索"></mt-search>
      <span v-if="searchValue.length>0">
      你正在搜索：<span class="key-word">{{searchValue}}</span>
      </span>-->
      <img
        class="search-icon search-left-icon"
        src="static/images/p_search/search-icon.png"
        alt
      >
      <input
        @keydown="searchKeyWord($event)"
        class="key-word"
        :placeholder="$t('home.search.placeholder')"
        v-model="keyWordTitle"
      >
      <img
        v-if="searchType==='1'"
        class="search-icon search-right-icon"
        src="static/images/p_search/right.png"
        alt
      >
      <img
        @click="del()"
        v-if="keyWordTitle.length>0?searchType==='2':''"
        class="search-icon search-right-icon"
        src="static/images/p_search/search-del.png"
        alt
      >
    </div>
    <!-- <router-link to='/'>  -->
    <span class="cancel" v-if="searchType==='2'" @click="toHomeSearch()">{{$t('home.search.cancel')}}</span>
    <!-- </router-link> -->
    <!-- router-link回到首页的search不用写样式 -->
  </div>
</template>

<script>
export default {
  data() {
    return {
      keyWordTitle: ""
      // searchValue: ""
    };
  },
  //props:['title'],接受父组件传过来的参数
  props: ["af", "searchType"],
  methods: {
    toSearch() {
      // let userInfo = {
      //   name: "xx",
      //   age: 18,
      //   sex: "女"
      // };
      this.$router.push({
        path: "/search"
        // query: { username: userInfo, pwd: "sss" } //传多个参数
      });
    },
    toHomeSearch() {
      this.$router.push({
        path: "/"
      });
    },
    del() {
      this.keyWordTitle = "";
    },
    searchKeyWord(e) {
      if (
        e.keyCode === 13 &&
        this.keyWordTitle.length > 0 &&
        this.keyWordTitle.replace(/\s+/g, "")
      ) {
        if (this.keyWordTitle.length > 5) {
          this.$emit("callBackSearch", this.keyWordTitle.slice(0, 5) + "...");
        } else {
          this.$emit("callBackSearch",this.keyWordTitle);
        }
      }
    }
  }
};
</script>

<style lang="scss" scoped>
.search-wp {
  display: flex;
  .search-w {
    width: 83%;
  }
  .cancel {
    line-height: 3.429rem;
    font-size: 0.79rem;
    letter-spacing: 0.003rem;
    opacity: 0.5;
  }
}
.search {
  position: relative;
  display: flex;
  align-items: center;
  height: 2.219rem;
  background-color: #ffffff;
  box-shadow: 0rem 0.156rem 0.147rem 0.009rem rgba(99, 99, 99, 0.08);
  border-radius: 0.313rem;
  border: solid 0.031rem #e5e5e5;
  margin: 0.563rem 0.75rem 1.156rem 0.75rem;
}
.search-icon {
  width: 0.75rem;
  height: 0.719rem;
  margin: 0rem 0.5rem 0rem 0.625rem;
}
.key-word {
  width: 80%;
  height: 0.79rem;
  letter-spacing: 0.003rem;
  color: #737373;
  border: 0;
}
.search-right-icon {
  position: absolute;
  right: 0.625rem;
}
</style>



