<template>
  <div class="footer-list">
    <div class="footer-list-frist">
      <ul>
        <li v-for="(item,index) in footers" :key="index">
          <!-- 左 -->
          <p class="footer-left">
            <img class="footer-img" :src="item.imgUrl" alt>
          </p>

          <!-- 中间 -->
          <p class="footer-center">
            <span class="musicname">{{item.title}}</span>
            <span class="singername">{{item.desc}}</span>
          </p>

          <!-- 右 -->
          <p class="footer-right">
            <img src="../../static/images/p_bottom/pre.png" alt>
            <img src="../../static/images/p_bottom/play.png" alt>
            <img src="../../static/images/p_bottom/next.png" alt>
          </p>
        </li>
      </ul>
    </div>
    <div class="footer-last">
      <ul>
        <li>
          <img class="last-img" src="static/images/p_bottom/voice.png" alt>
          <img class="last-img" src="static/images/p_bottom/energy.png" alt>
          <!-- <mt-progress :value="20" :bar-height="20" :bar-width="30"></mt-progress> -->
          <img class="last-img" src="static/images/p_bottom/random.png" alt>
          <img class="last-img" src="static/images/p_bottom/collected.png" alt>
          <img class="last-img" src="static/images/p_bottom/looked.png" alt>
          <img class="last-img" src="static/images/p_bottom/sleeped.png" alt>
          <img
            class="last-img"
            src="static/images/p_bottom/music-list.png"
            alt
            @click="showPlayList()"
          >
        </li>
      </ul>
    </div>
    <mt-popup v-model="bottomPopup" position="bottom">
      <div class="music-list">
        <p class="music-top">播放列表</p>
        <ul>
          <li v-for="(item,index) in musics" :key="index">
            <img :src="item.musicImg" alt class="music-img">
            <p>
            <span class="music-name">{{item.musicname}}</span>
            <span class="singer-name">{{item.songname}}</span>
            </p>
            <p class="song-time">{{item.songtime}}</p>
          </li>
        </ul>
