          songname: "李荣浩",
          songtime: "04:50"
        },
        {
          musicImg: "static/images/p_bottom/unplay.png",
          musicname: "年少有为",
          songname: "李荣浩",
          songtime: "04:50"
        },
        {
          musicImg: "static/images/p_bottom/unplay.png",
          musicname: "年少有为",
          songname: "李荣浩",
          songtime: "04:50"
        },
        {
          musicImg: "static/images/p_bottom/unplay.png",
          musicname: "年少有为",
          songname: "李荣浩",
          songtime: "04:50"
        },
        {
          musicImg: "static/images/p_bottom/unplay.png",
          musicname: "年少有为",
          songname: "李荣浩",
          songtime: "04:50"
        },
        {
          musicImg: "static/images/p_bottom/unplay.png",
          musicname: "年少有为",
          songname: "李荣浩",
          songtime: "04:50"
        },
        {
          musicImg: "static/images/p_bottom/unplay.png",
          musicname: "年少有为",
          songname: "李荣浩",
          songtime: "04:50"
        }
      ]
    };
  },
  methods: {
    showPlayList() {
      this.bottomPopup = true;
    }
  }
};
</script>
<style lang="scss" scoped>
.footer-list {
  position: fixed;
  bottom: 0;
}
p {
  display: inline-block;
}
ul li {
  display: flex;
}
.footer-list-frist {
  box-shadow: 0 -0.3rem 0.8rem rgb(179, 174, 174);
  background-color: #ebebeb;
  margin-top: 3rem;
}
.footer-img {
  height: 2.563rem;
  margin: 0.813rem 0 1rem 0.813rem;
}
.footer-center {
  width: 100%;
}
.musicname {
  display: block;
  font-size: 0.79rem;
  line-height: 0.844rem;
  font-weight: bold;
  margin: 1.094rem 0 0 0.844rem;
}
.singername {
  font-size: 0.561rem;
  line-height: 0.844rem;
  letter-spacing: -0.012rem;
  color: #636363;
  margin: 0.25rem 0 0 0.906rem;
}
.footer-right {
  display: flex;
}
.footer-right img:nth-child(1) {
  // width: 1.25rem;
  height: 0.875rem;
  margin: 1.844rem 1.625rem 1.656rem 0;
}
.footer-right img:nth-child(2) {
  // width: 2.875rem;
  height: 2.875rem;
  margin: 0.875rem 1.438rem 0.625rem 0;
}
.footer-right img:nth-child(3) {
