}

// footer-last
.footer-last {
  width: 23.438rem;
  height: 2.844rem;
  background-color: #ffffff;
}
.footer-last img:nth-child(1) {
  width: 1.156rem;
  height: 1.156rem;
  margin: 0.844rem auto;
}
.footer-last img:nth-child(2) {
  width: 1.625rem;
  height: 0.875rem;
  margin: 1.031rem auto 0.938rem auto;
}
.footer-last img:nth-child(3) {
  width: 1.031rem;
  height: 1rem;
  margin: 1rem auto 0.844rem auto;
}
.footer-last img:nth-child(4) {
  width: 1.281rem;
  height: 1.188rem;
  margin: 0.813rem auto 0.844rem auto;
}
.footer-last img:nth-child(5) {
  width: 1.063rem;
  height: 1.375rem;
  margin: 0.719rem auto 0.75rem auto;
}
.footer-last img:nth-child(6) {
  width: 1.344rem;
  height: 1.344rem;
  margin: 0.75rem auto;
}
.footer-last img:nth-child(7) {
  width: 1.219rem;
  height: 1.219rem;
  margin: 0.813rem auto;
}

//播放器
.music-top{
  width: 100%;
  height: 3rem;
  line-height: 3rem;
  text-align: center;
  background: rgba(229, 229, 229, 0.8);
}
.footer-list .music-list {
  background: #fff;
  width: 23.438rem;
}
.music-list ul {
  height: 50vh;
  overflow: hidden;
  overflow-y: scroll;
  -webkit-overflow-scrolling: touch;//ios滚动兼容;
}
.music-list li{
  display: flex;
  flex-direction: row wrap;
  justify-content: flex-start;
  align-items: center;
  padding: 1rem 1rem;
  border-bottom: 1px solid #f7f7f7;
}
.music-list ul li img{
  width: 2rem;
  height: 2rem;
  border: 1px solid #ddd;
  border-radius: 50%;
}
.music-list ul li span{
  display: flex;
  flex-direction: column;
  font-size: 16px;
  margin-left: 1rem;
}
.music-list ul li span:nth-child(2){
  font-size: 12px;
  color: #636363;
  margin-top: 0.2rem;
}
.song-time{
  margin: auto 1rem auto auto;
}
</style>



