<template>
  <div class="entirety">
    <!-- 头 -->
    <sy-header></sy-header>

    
    <!-- 搜索框 -->
      <sy-search  :af=false searchType='1'></sy-search>
    <!-- 顶部导航栏 -->
    <sy-navbar :navbars="navbars" chooesType="1"></sy-navbar>

    <!-- 轮播图 -->
    <sy-banner :banners="banners" bannersType="1" :time="4000"></sy-banner>

    <!-- 精品部分 -->
    <sy-container
      :containImgs="containImgs"
      :containerhots="containerhots"
      v-on:callbackHeadline="goPinkMore"
    ></sy-container>

    <!-- 导航栏文字的测试 -->
    <sy-navbar :navbars="navbars" chooesType="2"></sy-navbar>

    <!-- 轮播图测试 -->
    <sy-banner :banners="banners" bannersType="2" :time="2000"></sy-banner>

    <!-- 精品的测试的排行榜 -->
    <sy-container
      :containerhots="containerhots1"
      containerType="2"
      v-on:callbackHeadline="goRankMore"
    ></sy-container>
    <sy-rankinglist></sy-rankinglist>

    <!-- 精品的测试的底部 -->
    <div @click="toFavorite()">
      <sy-container :containerhots="containerhots2" chooeshot="2"></sy-container>
    </div>
    <!-- 页面底部 -->
    <sy-footer></sy-footer>
  </div>
</template>
<script>
import search from "../../components/search.vue";
import Header from "../../components/header.vue";
import banner from "../../components/banner.vue";
import navbar from "../../components/navbar.vue";
import container from "../../components/container.vue";
// import recommendation from "../../components/recommendation.vue";
import rankinglist from "../../components/rankinglist.vue";
import footer from "../../components/footer.vue";
export default {
  methods: {
    toFavorite() {
      this.$router.push({
        path: "/favorite"
      });
    },
    goPinkMore(e) {
      console.log(e);
      this.$router.push({
        path: "/recommended"
      });
    },
    goRankMore(e) {
      console.log(e);
      this.$router.push({
        path: "/rankpage"
      });
    },
    
  },
    components: {
      sySearch: search,
      syHeader: Header,
      syBanner: banner,
      syNavbar: navbar,
      syContainer: container,
      // syRecommendation: recommendation,
      syRankinglist: rankinglist,
      syFooter: footer
    },
    data() {
      return {
        banners: [
          {
            id: "1",
            imgUrl: "static/images/Inuyasha2.jpg"
          },
          {
            id: "2",
            imgUrl: "static/images/Inuyasha1.jpg"
          },
          {
            id: "3",
            imgUrl: "static/images/Inuyasha3.jpg"
          },
          {
            id: "4",
            imgUrl: "static/images/Inuyasha4.jpg"
          }
        ],
        navbars: [
          {
            id: "1",
            navbarImg: "static/images/assort/1.png",
            navbarTitle: this.$t('home.sort.sortTitle')
          },
          {
            id: "2",
            navbarImg: "static/images/assort/2.png",
            navbarTitle: this.$t('home.sort.sortTitle1')
          },
          {
            id: "3",
            navbarImg: "static/images/assort/3.png",
            navbarTitle: this.$t('home.sort.sortTitle2')
          },
          {
            id: "4",
            navbarImg: "static/images/assort/4.png",
            navbarTitle: this.$t('home.sort.sortTitle3')
          },
          {
            id: "5",
            navbarImg: "static/images/assort/5.png",
            navbarTitle: this.$t('home.sort.sortTitle4')
          }
        ],
        containerhots: {
          product: this.$t('favorite.product'),
          hot: this.$t('favorite.hot')
        },
        containerhots1: {
          product: this.$t('favorite.product1'),
          hot: this.$t('favorite.hot1')
        },
        containerhots2: {
          product: this.$t('favorite.product2'),
          hot: this.$t('favorite.hot2')
        },
        containImgs: [
          {
            id1: "1",
            imgUrl1:
              "static/images/p_handpick/1.png",
            imgUrl2:
              "static/images/p_handpick/2.png",
            musicsymbol: "static/images/p_handpick/music.png",
            musicmount: "17首"
          },
          {
            id1: "2",
            imgUrl1:
              "static/images/p_handpick/3.png",
            imgUrl2:
              "static/images/p_handpick/4.png",
            musicsymbol: "static/images/p_handpick/music.png",
            musicmount: "17首"
          },
          {
            id1: "3",
            imgUrl1:
              "static/images/p_handpick/5.png",
            imgUrl2:
              "static/images/p_handpick/6.png",
            musicsymbol: "static/images/p_handpick/music.png",
            musicmount: "17首"
          }
        ]
      };
    }
  }
</script>
<style scoped>
.entirety {
  padding-bottom: 9.5rem;
}
</style>



